import React from 'react'
import { Link } from 'react-router-dom';
import styles from "./home.module.css"
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, { Autoplay } from "swiper";
import "swiper/css";
SwiperCore.use([Autoplay]);

const Home = () => {
  return (
    <div className={styles.container}>
      <div className={styles.card_container}>
        <div className={styles.card_text}>
          <div className={styles.card_main_text}>
            카페 음료
            <br />
            <span className={styles.text_color}>
              <span className={styles.text_color1}>M</span>
              <span className={styles.text_color2}>B</span>
              <span className={styles.text_color3}>T</span>
              <span className={styles.text_color4}>I</span>
            </span>
            테스트
          </div>
          <div className={styles.card_sub_text}>나의 카페 음료는?</div>
        </div>
        <Swiper className={styles.card_image_container} loop={true} autoplay={{delay: 1000}}>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ISTJ.png`)} /><div className={styles.card_image_text}>에스프레소</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ISFJ.png`)} /><div className={styles.card_image_text}>아메리카노</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/INTP.png`)} /><div className={styles.card_image_text}>카페모카</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/INTJ.png`)} /><div className={styles.card_image_text}>카푸치노</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ESFJ.png`)} /><div className={styles.card_image_text}>바닐라라떼</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ESTJ.png`)} /><div className={styles.card_image_text}>헤이즐넛라떼</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ENTJ.png`)} /><div className={styles.card_image_text}>카라멜마끼아또</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ENTP.png`)} /><div className={styles.card_image_text}>아포가토</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ESTP.png`)} /><div className={styles.card_image_text}>레모네이드</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ESFP.png`)} /><div className={styles.card_image_text}>자몽에이드</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ENFJ.png`)} /><div className={styles.card_image_text}>유자에이드</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ENFP.png`)} /><div className={styles.card_image_text}>라임에이드</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/INFJ.png`)} /><div className={styles.card_image_text}>얼그레이 티</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/INFP.png`)} /><div className={styles.card_image_text}>캐모마일 티</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ISTP.png`)} /><div className={styles.card_image_text}>자스민 티</div></SwiperSlide>
          <SwiperSlide className={styles.card_image_position}><img className={styles.card_image} src={require(`./images/ISFP.png`)} /><div className={styles.card_image_text}>히비스커스 티</div></SwiperSlide>
        </Swiper>
        <div className={styles.card_button}>
          <Link className={styles.card_button_text} to="/survey">테스트 시작하기</Link>
        </div>
      </div>
    </div>
  );
}

export default Home;