import React, { useState } from "react";
import { useLocation } from "react-router-dom";
import styles from "./result.module.css"
import { isAndroid, isIOS } from "react-device-detect";

const Result = (props) => {
  const location = useLocation();
  const EI = location.state?.EI;
  const SN = location.state?.SN;
  const TF = location.state?.TF;
  const JP = location.state?.JP;
  const [mbti, settmbti]= useState(EI + SN + TF + JP);

  const CardImageText = () => {
    if (mbti === "ISTJ") {
      return "에스프레소"
    } else if (mbti === "ISFJ") {
      return "아메리카노"
    } else if (mbti === "ESTJ") {
      return "헤이즐넛라떼"
    } else if (mbti === "ESFJ") {
      return "바닐라라떼"
    } else if (mbti === "INTP") {
      return "카페모카"
    } else if (mbti === "INTJ") {
      return "카푸치노"
    } else if (mbti === "ENTJ") {
      return "카라멜마끼아또"
    } else if (mbti === "ENTP") {
      return "아포가토"
    } else if (mbti === "ISTP") {
      return "자스민 티"
    } else if (mbti === "ISFP") {
      return "히비스커스 티"
    } else if (mbti === "ESTP") {
      return "레모네이드"
    } else if (mbti === "ESFP") {
      return "자몽에이드"
    } else if (mbti === "INFJ") {
      return "얼그레이 티"
    } else if (mbti === "INFP") {
      return "캐모마일 티"
    } else if (mbti === "ENFJ") {
      return "유자에이드"
    } else if (mbti === "ENFP") {
      return "라임에이드"
    } else {
      return "정보 없음";
    }
  };

  const CardExplanationText = () => {
    if (mbti === "ISTJ") {
      return (`
다른 사람의 생각보다 자신의 생각이 더 중요하다.
공감보다 해결책을 제시하는 편이다.
다른 사람의 감정을 쉽게 헤아리지 못한다.
즉흥적인 것을 싫어하며 계획적이다.
장남, 장녀라는 소리 주변에서 많이 듣는다.
매우 현실적이다.
아부 잘 못하고, 마음에 없는 소리 죽어도 안한다.
모든 일을 가장 효율적인 방향으로 계산하며 실행한다.
대인관계에서 선긋는 것을 잘한다.
창의력이 부족한 편이다.
      `);
    } else if (mbti === "ISFJ") {
      return (`
이미지 관리 잘하고, 주변 사람들에게 좋은 인상으로 남아있다.
게으르지만 약속은 칼같이 지킨다.
거절을 잘 못한다.
공과 사 구분이 확실하다.
억울할 때 눈물부터 나온다.
말싸움 등의 갈등 상황은 최대한 피하려고 한다.
답장이 늦지만 읽씹, 안읽씹은 하지 않는다.
즉흥적인 것을 매우 싫어한다.
나서는 것을 좋아하지 않는다.
일을 크게 키우는 것을 싫어하고, 웬만하면 조용히 해결한다.
      `);
    } else if (mbti === "ESTJ") {
      return (`
줏대 없는 사람 싫어한다.
다른 사람들을 위해 쓰는 시간이 아깝다고 생각한다.
매우 계획적으로 활동한다.
팀장 등의 관리자 직책을 가진 경우가 많다.
가방 속에 항상 많은 것들을 가지고 다닌다.
승부욕이 매우 강한 편이다.
공감능력이 낮다.
한 번 할 때 제대로 하지 못할 것 같으면 포기한다.
팀플에서 자신은 버스 운전기사인 경우가 많다.
나서는 것을 좋아한다.
      `);
    } else if (mbti === "ESFJ") {
      return (`
계획대로 활동하지 않으면 병 생기는 사람이다.
세일할 때 많이 사두고 두고두고 쓴다.
먼저 나서지는 않지만, 리더쉽이 강하다.
한번 시작하면 꾸준히 오래 한다.
포커페이스 잘한다.
사람을 좋아하고 정이 많다.
말이 많지만, 개인적인 얘기는 잘 안 한다.
주변 사람들에게 좋은 인상으로 남아있다.
겉보기와 다르게 입이 매우 무겁다.
눈치가 매우 빠른 편이다.
      `);
    } else if (mbti === "INTP") {
      return (`
마감 기한을 못 지킨 경험이 여러 번 있다.
윗사람일지라도 할 말은 무조건 해야 한다.
돌발상황이 발생했을 때 대처하기 힘들어한다.
기념일에 큰 의미를 두지 않는다.
게으르지만 완벽주의자가 되고 싶어 한다.
계획 세우는 것을 싫어하며, 즉흥적이다.
집착하는 것을 싫어한다.
스스로를 선하다고 생각하지 않는다.
대부분 무교이다.
혼자 있는 것을 좋아한다.
      `);
    } else if (mbti === "INTJ") {
      return (`
공감능력이 낮다.
말주변이 없지만 할 말은 다 한다.
아부 잘 못하고, 마음에 없는 소리 죽어도 안한다.
가난하지만 않으면 된다는 마인드이다.
무식한 사람과는 절대 상종 안 한다.
다른 사람이 자신에게 참견하는 행동 못 참는다.
눈치가 매우 빠른 편이다.
자신의 가치관이 뚜렷하다.
자신의 일은 스스로 해결한다.
일 잘 못하는 사람은 한심하다고 생각한다.
      `);
    } else if (mbti === "ENTJ") {
      return (`
자존감이 매우 높다.
덕질을 잘 안 한다.
관계를 끊는 것에 대해 미련이 없다.
거절 잘한다.
욕심이 많다.
공감보다 해결책을 제시하는 편이다.
자신의 약점이 드러나는 것을 매우 싫어한다.
다른 사람이 욕해도 자신은 마이웨이다.
자랑하는 것을 즐긴다.
이익이 없는 것은 굳이 하지 않는다.
      `);
    } else if (mbti === "ENTP") {
      return (`
자신의 가치관이 매우 뚜렷하다.
자존감이 매우 높다.
모든 말에 반박을 잘한다.
승부욕이 매우 높다.
공감보다 해결책을 제시하는 편이다.
다른 사람의 상황은 중요하지 않다고 생각하는 편이다.
방이 매우 더럽다.
주변의 시선 따위는 신경 쓰지 않는다.
인간관계가 대부분 가볍다.
매우 즉흥적이다.
      `);
    } else if (mbti === "ISTP") {
      return (`
자신의 일이 아니면 관심이 없다.
관계를 끊는 것에 대해 미련이 없다.
수직적인 관계를 싫어하며, 수평적인 관계를 좋아한다.
사람과 친해질 필요를 못 느낀다.
읽씹, 안읽씹 고수다.
겉보기에 차가워 보인다는 소리를 많이 듣는다.
노력해서 받은 A보다, 노력하지 않고 받은 B에 만족한다.
"굳이"라는 말을 못 하면 대화를 못 한다.
자신의 약점이 드러나는 것을 매우 싫어한다.
귀차니즘의 끝판왕이다.
      `);
    } else if (mbti === "ISFP") {
      return (`
인간관계가 한정적인 편이다.
집에 있는 것을 매우 좋아하며, 소파 또는 침대와 물아일체이다.
드라마나 영화 몰아보기 잘한다.
상상으로 계획은 잘 세우지만 정작 실천은 하지 못한다.
보통 약속이 잡히는 쪽이며, 약속이 취소되기를 기대한다.
감성이 풍부하고, 눈물이 많다.
매우 친한 친구들 사이에서 또라이라고 불린다.
다른 사람들 눈치를 많이 본다.
영혼이 없어 보인다는 소리 자주 듣는다.
은근히 승부욕이 강하다.
      `);
    } else if (mbti === "ESTP") {
        return (`
매우 직설적이다.
가치관이 뚜렷하다.
포커페이스 잘 못한다.
매우 즉흥적이다.
눈치가 조금 없다.
자신이 보고 싶은 것만 본다.
자존감이 높다.
돌발상황이 발생했을 때 대처를 잘한다.
뒤끝이 없는 편이다.
인생을 매우 재밌게 산다.
      `);
    } else if (mbti === "ESFP") {
      return (`
자존감이 높지만, 남들과 비교할 때는 자존감이 급격히 낮아진다.
이미지 관리 잘한다.
매우 매우 활발하다.
언제나 흥이 넘쳐 주변에서 연예인 해도 되겠다는 소리 많이 듣는다.
기분이 하루에도 수십번 변한다.
사교성 만렙이며, 처음 본 사람과 잘 논다.
신중한 선택을 하기를 어려워한다.
아부 잘한다.
혼자 있는 것을 싫어한다.
사람을 좋아하고 정이 많다.
      `);
    } else if (mbti === "INFJ") {
      return (`
공감능력이 높다.
힘든 일이 있어도 잘 내색하지 않는다.
마음이 여리다.
꼼꼼한 성격이다.
어색한 건 싫어서 아무 말이나 막 뱉는다.
주변의 눈치를 매우 많이 본다.
종교를 많이 믿는 편이다.
말싸움 등의 갈등 상황은 최대한 피하려고 한다.
정의로운 사회를 만들기 위해 노력한다.
첫인상과 현인상이 매우 다른 편이다.
      `);
    } else if (mbti === "INFP") {
      return (`
계획은 세우지만, 실천은 하지 못한다.
충동구매를 매우 자주 한다.
약속 시간을 지키지 못한 경험이 여러 번 있다.
망상을 매우 자주 하며, 자신만의 소설을 쓴 적이 있는 편이다.
방이 매우 더럽다.
규칙을 잘 지키는 편이다.
정리하는 것을 좋아하지만, 정리하다가 포기하는 경우가 대부분이다.
종교는 싫지만, 미신은 믿는 편이다.
감성이 매우 풍부하다.
눈물이 매우 매우 많은 편이다.
      `);
    } else if (mbti === "ENFJ") {
      return (`
이미지 관리에 매우 집착하는 편이다.
눈치를 많이 본다.
사진찍기나 글쓰기가 취미인 편이다.
기념일을 중요하게 생각한다.
감성이 풍부하다.
구매 후기를 잘 남긴다.
사람을 좋아하고 정이 많다.
배려심이 깊다.
사람 만나는 것을 좋아하지만 자신과의 시간도 중요하게 생각한다.
주변 사람들에게 좋은 인상으로 남아있다.
      `);
    } else if (mbti === "ENFP") {
      return (`
눈치가 빠른 편이다.
감성이 풍부하다.
매우 매우 활발하다.
귀가 얇은 편이며, 쉽게 유혹당한다.
금사빠 성격이다.
웃음이 매우 많다.
매우 즉흥적이다.
사람 만나는 것을 좋아한다.
리액션을 매우 잘한다.
상황을 회피하기 위한 거짓말을 잘한다.
      `);
    } else {
      return `정보 없음`;
    }
  };

  const instagram_share = () => {
    if (isAndroid) {
      const url = "intent://instagram.com/#Intent;scheme=https;package=com.instagram.android;end";
      window.location.replace(url);
    } else if (isIOS) {
      window.location.replace("instagram://");
      setTimeout(() => {
        window.location.replace("https://apps.apple.com/us/app/instagram/id389801252");
      }, 10000);
    } else {
      window.location.replace("https://instagram.com");
    }
  }

  return (
    <div className={styles.container}>
      <div className={styles.card_container}>
        <div className={styles.card_image_container}>
          <div className={styles.card_text}>나의 카페 음료는?</div>
          <img className={styles.card_image} src={require(`./images/${mbti}.png`)} />
          <div className={styles.card_image_text}>{CardImageText()}</div>
        </div>
        <div className={styles.card_explanation_container}>
          <div className={styles.card_explanation_text}>
            {CardExplanationText()}
          </div>
        </div>
      </div>
      <div className={styles.again_container}>
        <div className={styles.again_image_container}>
          <img className={styles.again_image} src={require(`./images/again.png`)} />
        </div>
        <div className={styles.again_text}>다시하기</div>
      </div>
      <div className={styles.again_container}>
        <div className={styles.again_image_container}>
          <img className={styles.again_image} src={require(`./images/again.png`)} />
        </div>
        <div className={styles.again_text} onClick={() => instagram_share()}>인스타그램 공유하기</div>
      </div>
    </div>
  );
}

export default Result;
